
body {
  align-items: center;
  justify-content: center;
  margin:0;
  background-color: #fafafa
}
.login-modal-mask {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 9998;
  background: rgba(33, 33, 33, 0.43);
}
.login-modal-card {
  background: #fff;
  max-width: 300px;
  margin: 80px auto 0 auto;
  box-shadow: 0 1px 6px rgba(0,0,0,0.35);
  padding: 16px;
  border-radius: 3px;
}
.example-appbar{
  position: fixed;
  left: 256px;
  right: 0;
  top: 0;
  width: auto;
  transition: all .45s cubic-bezier(.23,1,.32,1);
  z-index: 998;
}
.example-appbar.nav-hide {
  left: 0;
}
.example-content{
  padding-top: 56px;
  padding-left: 256px;
  transition: all .45s cubic-bezier(.23,1,.32,1)
}
.example-content.nav-hide {
  padding-left: 0;
}
.content-wrapper{
  padding: 48px 72px;
}
@media (min-width: 480px) {
.example-content{
    padding-top: 64px;
}
}
@media (max-width: 993px) {
.example-appbar {
    left: 0;
}
.example-content{
    padding-left: 0;
}
.content-wrapper {
    padding: 24px 36px;
}
}



